body {
  width: 100vw;
  height: 100vh;
  background-image: url("bg.bc9809e7.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0;
  font-family: sans-serif;
  overflow: hidden;
}

#world {
  z-index: 3;
  pointer-events: none;
  position: absolute;
  top: 0;
}

h1 {
  text-align: center;
  z-index: 4;
  margin: 0;
  font-family: Arial;
  font-size: 3.7vw;
  display: inline-block;
  position: absolute;
  bottom: 5vh;
}

#feels-amazing-man {
  width: 25vw;
  height: 25vw;
  animation-name: rotate-1;
  animation-duration: 1.7s;
  animation-timing-function: steps(2, jump-none);
  animation-iteration-count: infinite;
  position: absolute;
  top: 20vh;
  left: 7vw;
  rotate: 20deg;
}

#point-right {
  height: 25vw;
  margin-top: 1%;
  animation-name: rotate-2;
  animation-duration: 1.7s;
  animation-timing-function: steps(2, jump-none);
  animation-iteration-count: infinite;
  position: absolute;
  top: 20vh;
  left: 38vw;
  rotate: -10deg;
}

@keyframes rotate-1 {
  to {
    rotate: -20deg;
  }
}

@keyframes rotate-2 {
  to {
    rotate: 10deg;
  }
}

.box {
  width: 20vw;
  height: 20vw;
  justify-content: center;
  display: flex;
  position: absolute;
  top: 20vh;
  left: 67vw;
}

.box-body {
  z-index: 2;
  height: 100%;
  width: 100%;
  background: linear-gradient(#762c2c, #ff0303);
  border-bottom-left-radius: 5%;
  border-bottom-right-radius: 5%;
  margin-top: 20%;
  position: relative;
  box-shadow: 0 4px 8px #0000004d;
}

.box-body:hover {
  cursor: pointer;
}

.box-body:after {
  content: "";
  width: 50px;
  background: linear-gradient(#fff, #ffefa0);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.img {
  width: 13vw;
  height: 13vw;
  z-index: 1;
  align-self: flex-end;
  position: absolute;
}

.box-lid {
  z-index: 1;
  height: 40px;
  height: 40px;
  width: 100%;
  background-color: #cc231e;
  border-radius: 5%;
  position: absolute;
  bottom: 90%;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 8px 4px -4px #0000004d;
}

.box-lid:after {
  content: "";
  width: 50px;
  background: linear-gradient(#ffefa0, #fff);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.box-bowtie {
  z-index: 1;
  height: 100%;
}

.box-bowtie:before, .box-bowtie:after {
  content: "";
  width: 83px;
  height: 83px;
  z-index: -1;
  border: 16px solid #fff;
  border-radius: 50% 50% 0;
  position: absolute;
  bottom: 100%;
}

.box-bowtie:before {
  left: 50%;
  transform: translateX(-100%)skew(10deg, 10deg);
}

.box-bowtie:after {
  left: 50%;
  transform: translateX(0%)rotate(90deg)skew(10deg, 10deg);
}

.img-animate {
  transition: all 1s .5s;
  transform: translateY(-100%);
}

.lid-animate {
  animation: 1s ease-in-out forwards box-lid;
}

@keyframes box-lid {
  0%, 20% {
    transform: translate(-50%)rotate(0);
  }

  50% {
    transform: translate(15%, -130%)rotate(25deg);
  }

  90%, 100% {
    transform: translate(30%, 325%)rotate(70deg);
  }
}

.area {
  text-align: center;
  letter-spacing: -3px;
  text-transform: uppercase;
  text-shadow: 0 0 15px #00f, 0 0 7px pink;
  color: #f0f0ff;
  font-weight: 700;
  animation: .8s ease-in-out infinite blur;
}

@keyframes blur {
  0% {
    transform: scale(1.3);
  }

  50% {
    color: #fffffa;
    text-shadow: 0 0 25px red, 0 0 5px red, 0 10px 100px purple, 0 -10px 100px pink;
    transform: scale(1);
  }

  100% {
    transform: scale(1.3);
  }
}

.message {
  width: 100vw;
  height: 100vh;
  z-index: 9;
  background-color: #000;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
}

.message button {
  font-size: 7vw;
}

.hide {
  opacity: 0;
  pointer-events: none;
  transition: all 1.5s;
}

.mobile {
  visibility: hidden;
  text-align: center;
  color: #fff;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  font-size: 7vw;
  display: flex;
}

.pc {
  justify-content: center;
  display: flex;
}

@media only screen and (max-device-width: 640px) {
  .mobile {
    visibility: visible;
  }

  #vika, .pc {
    visibility: hidden;
  }
}

